module.exports = "<Form>\n  <Form.Group controlId=\"exampleForm.SelectCustomHtmlSize\">\n    <Form.Label>Select with three visible options</Form.Label>\n    <Form.Control as=\"select\" htmlSize={3} custom>\n      <option>1</option>\n      <option>2</option>\n      <option>3</option>\n      <option>4</option>\n      <option>5</option>\n    </Form.Control>\n  </Form.Group>\n</Form>;\n"