module.exports = "<Form>\n  <Form.Row className=\"align-items-center\">\n    <Col xs=\"auto\">\n      <Form.Label htmlFor=\"inlineFormInput\" srOnly>\n        Name\n      </Form.Label>\n      <Form.Control\n        className=\"mb-2\"\n        id=\"inlineFormInput\"\n        placeholder=\"Jane Doe\"\n      />\n    </Col>\n    <Col xs=\"auto\">\n      <Form.Label htmlFor=\"inlineFormInputGroup\" srOnly>\n        Username\n      </Form.Label>\n      <InputGroup className=\"mb-2\">\n        <InputGroup.Prepend>\n          <InputGroup.Text>@</InputGroup.Text>\n        </InputGroup.Prepend>\n        <FormControl id=\"inlineFormInputGroup\" placeholder=\"Username\" />\n      </InputGroup>\n    </Col>\n    <Col xs=\"auto\">\n      <Form.Check\n        type=\"checkbox\"\n        id=\"autoSizingCheck\"\n        className=\"mb-2\"\n        label=\"Remember me\"\n      />\n    </Col>\n    <Col xs=\"auto\">\n      <Button type=\"submit\" className=\"mb-2\">\n        Submit\n      </Button>\n    </Col>\n  </Form.Row>\n</Form>;\n"